				</div>
	        </div>
		</div>
	</div>
</template>
<style>
  .el-table .warning-row {
    color: #b80000;
  }
</style>
<script type="text/javascript">
	import Place from './place'
	import interBtn from './../interactions'
	import * as echarts from 'echarts'
	import reqData from "../../../reqData.js"
	export default{
		name:'page1_2_4',
		data(){
			return{
				msg:["审计准备","审计标准及依据","勾稽关系"],
				selTypeOptions: [{
		          value: '1',
		          label: '属性筛选'
		        }, {
		          value: '2',
		          label: '维度表索引'
		        }],
		        selType:'维度表索引',
				selOptions: [{
		          value: '4578',
		          label: '货币资金'
		        }, {
		          value: '4546',
		          label: '平均资产总额'
		        }, {
		          value: '24245',
		          label: '预收账款'
		        }, {
		          value: '4567',
		          label: '速动比率'
		        }],
		        selAllValue: [],
		        selValueArr:[],
				tableData:[],
			}
		},
		components:{
			Place,
			interBtn
		},
		computed: {
		    
	    },
	    methods: {
	    	getSelAllData(){
	    		reqData
		        .req({
		          apiName: "DimensionSearch",
                  data: {             
	                word: '南京审计大学'
	              }
		        })
		        .then(res => {
		          res = res.data;
		          if (res && res.code === "200") {
		            let data = res.data;
		            console.log(data);
		            this.selOptions=data;
		          }
		        })
		        .catch(err => {
		          console.log(err);
		       });
	    	},
	       	selHandleChange(value){
		      	console.log(value);
		      	this.selValueArr=value;
		    },
	      	selSearch(){
		      	if(this.selValueArr.length>0){
		      		alert(this.selValueArr);
		      		getTableData(JSON.stringify(this.selValueArr))
		      	}else{
		      		alert("请输入检索条件")
		      	}
	      	},
	      	tableRowClassName({row, rowIndex}) {
		        if (row['是否异常'] === "是") {
		          return 'warning-row';
		        } 
		        return '';
	      	},
	      	getTableData(word){
	      		this.tableData=[];
	      		// if(word!=','){
	      		// 	word=JSON.stringify(word)
	      		// }
		      	// reqData
			      //   .req({
			      //     apiName: "FormulaRelation",
	        //           data: {             
		       //          word: word
		       //        }
			      //   })
			      //   .then(res => {
			      //     res = res.data;
			      //     if (res && res.code === "200") {
			      //       let data = res.data;
			      //       console.log(data);
			      //       this.tableData=data;
			      //     }
			      //   })
			      //   .catch(err => {
			      //     console.log(err);
			      //  });
			    let cw_data=sessionStorage.getItem("sjzhongdian_cw")?sessionStorage.getItem("sjzhongdian_cw"):JSON.stringify([]);
			    reqData
			        .req({
			          apiName: "FinanciInd",
			          data: {             
		                word:cw_data
		              }
			        })
			        .then(res => {
			          res = res.data;
			          if (res && res.code === "200") {
			            let data = res.data;
			            console.log(data);
			            this.tableData=data;
			          }
			        })
			        .catch(err => {
			          console.log(err);
			       });
	      	}
	    },
	    mounted(){
	    	this.getSelAllData();
	    	this.getTableData(',');


	    	$(document).ready(function(){
				$("#select1 dd").click(function () {
					$(this).addClass("selected").siblings().removeClass("selected");
					if ($(this).hasClass("select-all")) {
						$("#selectA").remove();
					} else {
						var copyThisA = $(this).clone();
						if ($("#selectA").length > 0) {
							$("#selectA a").html($(this).text());
						} else {
							$(".select-result dl").append(copyThisA.attr("id", "selectA"));
						}
					}
				});
				
				$("#select2 dd").click(function () {
					$(this).addClass("selected").siblings().removeClass("selected");
					if ($(this).hasClass("select-all")) {
						$("#selectB").remove();
					} else {
						var copyThisB = $(this).clone();
						if ($("#selectB").length > 0) {
							$("#selectB a").html($(this).text());
						} else {
							$(".select-result dl").append(copyThisB.attr("id", "selectB"));
						}
					}
				});
				
				$("#select3 dd").click(function () {
					$(this).addClass("selected").siblings().removeClass("selected");
					if ($(this).hasClass("select-all")) {
						$("#selectC").remove();
					} else {
						var copyThisC = $(this).clone();
						if ($("#selectC").length > 0) {
							$("#selectC a").html($(this).text());
						} else {
							$(".select-result dl").append(copyThisC.attr("id", "selectC"));
						}
					}
				});
				
				$("#selectA").live("click", function () {
					$(this).remove();
					$("#select1 .select-all").addClass("selected").siblings().removeClass("selected");
				});
				
				$("#selectB").live("click", function () {
					$(this).remove();
					$("#select2 .select-all").addClass("selected").siblings().removeClass("selected");
				});
				
				$("#selectC").live("click", function () {
					$(this).remove();
					$("#select3 .select-all").addClass("selected").siblings().removeClass("selected");
				});
				
				$(".select dd").live("click", function () {
					if ($(".select-result dd").length > 1) {
						$(".select-no").hide();
					} else {
						$(".select-no").show();
					}
				});
			});
	    }
	}
</script>
<style type="text/css" scoped="">
	.cont{
		margin: 20px auto;
		min-height: 600px;
	     background-color: #fff;
	    padding: 10px 40px;
	    border-radius: 8px;
	    background-color: #fff;
	}
	.el-table .warning-row {
		background: oldlace;
  	}

  	.el-table .success-row {
   		background: #f0f9eb;
  	}
  	.select{
  		padding:5px 10px;
  		border-bottom: #ddd 1px dashed;
	    width: 100%;
	    margin: 20px auto;
  		font-size:12px
  	}
	.select li{
		list-style:none;
		padding:10px 0 5px 100px
	}
	.select .select-list{
		border-bottom:#eee 1px dashed
	}
	.select dl{
		zoom:1;
		position:relative;
		line-height:24px;
	}
	.select dl:after{
		content:" ";
		display:block;
		clear:both;
		height:0;
		overflow:hidden
	}
	.select dt{
		width:100px;
		margin-bottom:5px;
		position:absolute;
		top:0;left:-100px;
		text-align:right;
		color:#666;
		height:24px;
		line-height:24px
	}
	.select dd{
		float:left;
		display:inline;
		margin:0 0 5px 5px;
	}
	.select a{
		display:inline-block;
		white-space:nowrap;
		height:24px;
		padding:0 10px;
		text-decoration:none;
		color:#000;
		border-radius:2px;
	}
	.select a:hover{
		color:#b80000;
		background-color:#fff2f2
	}
	.select .selected a{
		color:#fff;
		background-color:#b80000
	}
	.select-result dt{
		font-weight:bold
	}
	.select-no{
		color:#999
	}
	.select .select-result a{
		padding-right:20px;
		background:#b80000 url("../../../assets/image/page1_2/close.gif") right 9px no-repeat
	}
	.select .select-result a:hoverclose.gif{
		background-position:right -15px
	}
</style>


